.pick1 {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 250px;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.pick1::after {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  pointer-events: none;
}

.pick1:hover {
  -webkit-transform: scale(1, 1.2);
          transform: scale(1, 1.2);
}

.pick1:hover::after {
  opacity: 0;
}

.video-fluid1 {
  width: 100%;
